<template>
  <view class="profile-edit-container">
    <!-- Â§¥ÂÉè‰∏ä‰º†Âå∫Âüü -->
    <view class="avatar-section">
      <view class="avatar-wrapper" @tap="chooseAvatar">
        <image
          class="avatar-image"
          :src="formData.avatar || defaultAvatar"
          mode="aspectFill"
        ></image>
        <view class="avatar-mask">
          <text class="camera-icon">üì∑</text>
          <text class="change-text">Êõ¥Êç¢Â§¥ÂÉè</text>
        </view>
      </view>
      <text class="avatar-tip">ÁÇπÂáªÊõ¥Êç¢Â§¥ÂÉè</text>
    </view>

    <!-- Ë°®ÂçïÂå∫Âüü -->
    <view class="form-section">
      <!-- ÊòµÁß∞ -->
      <view class="form-item">
        <text class="form-label">ÊòµÁß∞</text>
        <input
          class="form-input"
          v-model="formData.nickname"
          placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞Ôºà2-20‰ΩçÔºâ"
          maxlength="20"
        />
      </view>

      <!-- ÊÄßÂà´ -->
      <view class="form-item">
        <text class="form-label">ÊÄßÂà´</text>
        <picker
          mode="selector"
          :range="genderOptions"
          :range-key="'label'"
          :value="genderIndex"
          @change="onGenderChange"
        >
          <view class="picker-value">
            {{ formData.gender !== null ? genderOptions[genderIndex].label : 'ËØ∑ÈÄâÊã©' }}
            <text class="arrow">‚Ä∫</text>
          </view>
        </picker>
      </view>

      <!-- ÈÇÆÁÆ± -->
      <view class="form-item">
        <text class="form-label">ÈÇÆÁÆ±</text>
        <input
          class="form-input"
          v-model="formData.email"
          placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±ÔºàÂèØÈÄâÔºâ"
        />
      </view>

      <!-- ÊâãÊú∫Âè∑ÔºàÂè™ËØªÔºâ -->
      <view class="form-item disabled">
        <text class="form-label">ÊâãÊú∫Âè∑</text>
        <view class="form-value">
          <text>{{ userInfo.mobile || 'Êú™ÁªëÂÆö' }}</text>
        </view>
      </view>
    </view>

    <!-- ‰øùÂ≠òÊåâÈíÆ -->
    <view class="submit-section">
      <button
        class="submit-btn"
        :class="{ disabled: !isFormValid }"
        :disabled="!isFormValid"
        @tap="saveProfile"
      >
        ‰øùÂ≠ò
      </button>
    </view>
  </view>
</template>

<script>
import { fetchUserInfo, updateUserInfo } from '@/api/user.js'
import { uploadAvatar } from '@/api/upload.js'
import { getUserInfo, setUserInfo } from '@/utils/token.js'

export default {
  data() {
    return {
      userInfo: {},
      defaultAvatar: 'https://via.placeholder.com/120',
      formData: {
        nickname: '',
        avatar: '',
        gender: null,
        email: ''
      },
      genderOptions: [
        { label: '‰øùÂØÜ', value: 0 },
        { label: 'Áî∑', value: 1 },
        { label: 'Â•≥', value: 2 }
      ],
      genderIndex: 0,
      uploading: false
    }
  },

  computed: {
    // Ë°®ÂçïÈ™åËØÅ
    isFormValid() {
      const { nickname } = this.formData
      return nickname && nickname.length >= 2 && nickname.length <= 20
    }
  },

  onLoad() {
    this.loadUserInfo()
  },

  methods: {
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    async loadUserInfo() {
      try {
        const result = await fetchUserInfo()
        if (result.data) {
          this.userInfo = result.data

          // Â°´ÂÖÖË°®ÂçïÊï∞ÊçÆ
          this.formData = {
            nickname: result.data.nickname || '',
            avatar: result.data.avatar || '',
            gender: result.data.gender,
            email: result.data.email || ''
          }

          // ËÆæÁΩÆÊÄßÂà´ÈÄâÊã©Âô®ÁöÑÁ¥¢Âºï
          if (result.data.gender !== null && result.data.gender !== undefined) {
            this.genderIndex = this.genderOptions.findIndex(
              item => item.value === result.data.gender
            )
          }
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error)

        // Â¶ÇÊûúAPIÂ§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÁºìÂ≠ò
        const localUserInfo = getUserInfo()
        if (localUserInfo) {
          this.userInfo = localUserInfo
          this.formData = {
            nickname: localUserInfo.nickname || '',
            avatar: localUserInfo.avatar || '',
            gender: localUserInfo.gender,
            email: localUserInfo.email || ''
          }
        }
      }
    },

    // ÈÄâÊã©Â§¥ÂÉè
    chooseAvatar() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'], // ÂéãÁº©Âõæ
        sourceType: ['album', 'camera'], // ‰ªéÁõ∏ÂÜåÈÄâÊã©ÊàñÊãçÁÖß
        success: (res) => {
          const tempFilePath = res.tempFilePaths[0]
          this.uploadAvatar(tempFilePath)
        }
      })
    },

    // ‰∏ä‰º†Â§¥ÂÉè
    async uploadAvatar(filePath) {
      if (this.uploading) {
        uni.showToast({
          title: '‰∏ä‰º†‰∏≠ÔºåËØ∑Á®çÂÄô',
          icon: 'none'
        })
        return
      }

      this.uploading = true

      uni.showLoading({
        title: '‰∏ä‰º†‰∏≠...'
      })

      try {
        const result = await uploadAvatar(filePath)

        if (result.code === 200 && result.data) {
          this.formData.avatar = result.data

          uni.showToast({
            title: '‰∏ä‰º†ÊàêÂäü',
            icon: 'success'
          })
        }
      } catch (error) {
        console.error('‰∏ä‰º†Â§¥ÂÉèÂ§±Ë¥•:', error)
        uni.showToast({
          title: '‰∏ä‰º†Â§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.uploading = false
        uni.hideLoading()
      }
    },

    // ÊÄßÂà´ÈÄâÊã©
    onGenderChange(e) {
      const index = e.detail.value
      this.genderIndex = index
      this.formData.gender = this.genderOptions[index].value
    },

    // ‰øùÂ≠ò‰∏™‰∫∫ËµÑÊñô
    async saveProfile() {
      if (!this.isFormValid) {
        return
      }

      // È™åËØÅÈÇÆÁÆ±Ê†ºÂºèÔºàÂ¶ÇÊûúÂ°´ÂÜô‰∫ÜÔºâ
      if (this.formData.email) {
        const emailReg = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        if (!emailReg.test(this.formData.email)) {
          uni.showToast({
            title: 'ÈÇÆÁÆ±Ê†ºÂºè‰∏çÊ≠£Á°Æ',
            icon: 'none'
          })
          return
        }
      }

      uni.showLoading({
        title: '‰øùÂ≠ò‰∏≠...'
      })

      try {
        // Âè™Êèê‰∫§ÊúâÂÄºÁöÑÂ≠óÊÆµ
        const updateData = {}
        if (this.formData.nickname) updateData.nickname = this.formData.nickname
        if (this.formData.avatar) updateData.avatar = this.formData.avatar
        if (this.formData.gender !== null && this.formData.gender !== undefined) {
          updateData.gender = this.formData.gender
        }
        if (this.formData.email) updateData.email = this.formData.email

        const result = await updateUserInfo(updateData)

        if (result.code === 200) {
          uni.showToast({
            title: '‰øùÂ≠òÊàêÂäü',
            icon: 'success'
          })

          // ÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
          await this.loadUserInfo()

          // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
          const updatedUserInfo = getUserInfo()
          if (updatedUserInfo) {
            setUserInfo(updatedUserInfo)
          }

          // Âª∂ËøüËøîÂõû‰∏ä‰∏ÄÈ°µ
          setTimeout(() => {
            uni.navigateBack()
          }, 1500)
        }
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        uni.hideLoading()
      }
    }
  }
}
</script>

<style scoped>
.profile-edit-container {
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-bottom: 120rpx;
}

/* Â§¥ÂÉèÂå∫Âüü */
.avatar-section {
  background: #fff;
  padding: 60rpx 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20rpx;
}

.avatar-wrapper {
  position: relative;
  width: 200rpx;
  height: 200rpx;
  margin-bottom: 20rpx;
}

.avatar-image {
  width: 100%;
  height: 100%;
  border-radius: 100rpx;
  border: 4rpx solid #f5f5f5;
}

.avatar-mask {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80rpx;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0 0 100rpx 100rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.camera-icon {
  font-size: 32rpx;
  line-height: 1;
}

.change-text {
  font-size: 20rpx;
  color: #fff;
  margin-top: 4rpx;
}

.avatar-tip {
  font-size: 26rpx;
  color: #999;
}

/* Ë°®ÂçïÂå∫Âüü */
.form-section {
  background: #fff;
}

.form-item {
  display: flex;
  align-items: center;
  padding: 30rpx;
  border-bottom: 2rpx solid #f5f5f5;
}

.form-item:last-child {
  border-bottom: none;
}

.form-label {
  width: 150rpx;
  font-size: 30rpx;
  color: #333;
  font-weight: 500;
}

.form-input {
  flex: 1;
  font-size: 28rpx;
  color: #333;
}

.form-value {
  flex: 1;
  font-size: 28rpx;
  color: #666;
}

.form-item.disabled {
  background: #f8f8f8;
}

.form-item.disabled .form-label,
.form-item.disabled .form-value {
  color: #999;
}

.picker-value {
  flex: 1;
  font-size: 28rpx;
  color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow {
  font-size: 40rpx;
  color: #999;
  font-weight: bold;
}

/* ‰øùÂ≠òÊåâÈíÆ */
.submit-section {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20rpx;
  background: #fff;
  box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);
}

.submit-btn {
  width: 100%;
  height: 88rpx;
  line-height: 88rpx;
  border-radius: 44rpx;
  background: #07c160;
  color: #fff;
  font-size: 32rpx;
  font-weight: bold;
  border: none;
}

.submit-btn::after {
  border: none;
}

.submit-btn.disabled {
  background: #ccc;
  color: #999;
}
</style>
